<template>
    <div>
        <div class="d-flex mt-2 mb-n2">
            <div class="d-flex mr-auto pl-4">
                {{ this.activeParentEntity && this.activeParentEntity.name }} (₱{{ this.activeParentEntity && this.activeParentEntity.itemAmount }})
            </div>
            <div class="d-flex drawer-actions">
                <v-btn @click.stop="onClickDecreaseSharedQty()" :disabled="sharedQty === 0" fab color="indigo" depressed class="xxs-btn" v-show="isItemShared">
                    <font-awesome-icon icon="minus" class="xxs-btnicon" color="white" />
                </v-btn>
            </div>
            <div class="d-flex drawer-qty">
               <v-text-field v-model.lazy="sharedQty" dense class="mt-n2" outlined v-show="isItemShared"></v-text-field>
            </div>
            <div class="d-flex drawer-actions mr-3">
                <v-btn @click.stop="onClickIncreaseSharedQty()" fab color="indigo" depressed class="xxs-btn" v-show="isItemShared">
                    <font-awesome-icon icon="plus" class="xxs-btnicon" color="white" />
                </v-btn>
            </div>
            <div class="d-flex">
                <v-checkbox dense class="mt-0" v-model="isItemShared" :label="`Shared`" hide-details></v-checkbox>
            </div>
            <div class="d-flex">
                <v-btn class="ml-2 mr-2" small outlined x color="indigo" @click.stop="onSaveList()">
                    Save
                </v-btn>
            </div>
        </div>
        <v-list nav dense class="bottom-drawer custom-scroll" :key="componentKey">
            <v-list-item-group active-class="deep-purple--text text--accent-4" v-if="listData.length">
                <v-list-item v-for="(person) in listData" :key="person.id">
                    <v-list-item-subtitle>
                        <v-checkbox v-model="person.isChecked" dense @change="onClickListItem(person)" :label="`${person.name}`" class="ml-3"></v-checkbox>
                        <div class="drawer-actions-container" v-show="!isItemShared">
                            <div class="d-flex drawer-actions">
                                <v-btn @click.stop="onClickDecreaseQty(person)" :disabled="person.currentQty === 0" fab color="indigo" depressed class="xxs-btn">
                                    <font-awesome-icon icon="minus" class="xxs-btnicon" color="white" />
                                </v-btn>
                            </div>
                            <div class="d-flex drawer-actions drawer-qty">
                                <v-text-field  v-model.lazy="person.currentQty" dense class="mt-n2" outlined></v-text-field>
                            </div>
                            <div class="d-flex drawer-actions">
                                <v-btn @click.stop="onClickIncreaseQty(person)" fab color="indigo" depressed class="xxs-btn">
                                    <font-awesome-icon icon="plus" class="xxs-btnicon" color="white" />
                                </v-btn>
                            </div>
                        </div>
                    </v-list-item-subtitle>
                </v-list-item>
            </v-list-item-group>
        </v-list>
    </div>
</template>

<script>
    export default {
        data: () => ({
            //  bottomDrawerList: [],
            isItemShared: false,
            componentKey: 0,
            sharedQty: 1
        }),
        props: {
            listData: {
                type: Array,
            },
            activeParentEntity: {
                type: Object,
            },
        },
        watch: {
            listData(newValue) {
                this.listData = newValue;
                this.isItemShared = this.activeParentEntity.isItemShared; 
                this.sharedQty = this.isItemShared && this.activeParentEntity.itemTotalQty ? this.activeParentEntity.itemTotalQty : 1;
            },
        },
        created: function() {
            if (this.activeParentEntity) {
                this.isItemShared = this.activeParentEntity.isItemShared;
            }
        },
        methods: {
            forceRerender() {
                this.componentKey += 1;
            },
            onSaveList: function() {
                let dataObj = {
                    drawerList: this.listData,
                    activeParentEntity: this.activeParentEntity,
                    isItemShared: this.isItemShared 
                };
                if (this.isItemShared) dataObj.sharedQty = this.sharedQty;
                this.$emit("onChangePersonDrawerCheckbox", dataObj);
            },
            onClickListItem: function(person) {
                person.currentQty = !person.isChecked && person.currentQty !== 0 ? 0 : 1;
                this.forceRerender();
                //this.$forceUpdate()
            }, 
            onClickDecreaseQty: function(person) {
                person.currentQty--;
                if (person.currentQty && !person.isChecked) person.isChecked = true;
                if (!person.currentQty) person.isChecked = false;
                this.forceRerender(); 
            },
            onClickIncreaseQty: function(person) { 
                person.currentQty++;
                if (person.currentQty && !person.isChecked) person.isChecked = true;
                this.forceRerender(); 
            },
            onClickDecreaseSharedQty: function() { 
                this.sharedQty--; 
            },
            onClickIncreaseSharedQty: function() { 
                this.sharedQty++; 
            },
        },
    };
</script>
