
<template>
  <v-dialog @input="closeModal()" v-model="displayDialog" @keydown.esc="closeModal" max-width="500px">
    <v-card v-if="addItemsList.length">  
       <v-list>
         <div class="ml-6 mr-6">
            <v-text-field label="Group Name" v-model="newGroupName">
                        </v-text-field>
         </div>
      <v-list-item v-for="item in addItemsList" :key="item.id">
                    <v-list-item-subtitle >
                        <v-checkbox v-model="item.isChecked" dense  
                        :label="`${item.name}`" 
                        class="ml-2" > </v-checkbox>  
                    </v-list-item-subtitle>
                </v-list-item>
      </v-list>
      <v-divider></v-divider>
      <v-card-actions class="d-flex">
        <div class="mr-auto"></div>
        <div>
        <v-btn color="blue darken-1" text @click="saveCheckedItems">
          Add
        </v-btn></div>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>  
<script>
   export default {
     data: () => ({ 
       displayDialog: false,
       addItemsList: [],
       newGroupName: "" 
    }),
     props: {
       showSaveItemsModal: {
         type: Boolean,
         default: false
       },
       itemList: {
         type: Array
       }
     }, 
     watch:{
       showSaveItemsModal(newValue){
         this.displayDialog = newValue;  
       }, 
       itemList(newList){
         this.addItemsList = newList.map((item) => { 
           return { id: item.id, name: item.name, itemAmount: item.itemAmount, isChecked: true };
         }); 
       }, 
     },
     methods: { 
       closeModal() {   
         this.newGroupName = "";
        //  this.addItemsList = [];
          this.$emit('showAddItemsToGroup', { hideModal: true });
       } , 
       saveCheckedItems() {    
         if (this.newGroupName){
           let finalItemsList = this.addItemsList.filter(x => x.isChecked);
          this.$emit("submitItemsToGroup", {children: finalItemsList, groupName: this.newGroupName});
          this.closeModal();
        } 
       } 
     }
   }
</script>