<template>
    <div>
        <v-tabs v-model="tab" color="indigo">
            <v-tabs-slider></v-tabs-slider>
            <v-tab>Entries</v-tab>
            <v-tab>Items</v-tab>
            <v-tab>Members</v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
            <v-tab-item :key="0">
                <v-row no-gutters class="">
                    <v-container v-show="!showEditInputEntry">
                        <v-btn outlined block color="indigo" class="" @click="onToggleEditInputEntry(showEditInputEntry)">
                            <font-awesome-icon icon="plus" size="sm" class="mr-1" /> ADD ENTRY
                        </v-btn>
                    </v-container>
                    <v-container v-show="showEditInputEntry">
                        <div class="d-flex">
                            <div class="flex-grow-1">
                                <v-text-field ref="entryName" label="Name" v-model="newEntryName" @keydown.enter="storeEntry" class="mr-3"></v-text-field>
                            </div>
                            <div class="mt-2">
                                <v-btn outlined fab color="indigo" @click.stop="storeEntry" class="xxs-btn mr-1">
                                    <font-awesome-icon icon="check" class="xxs-btnicon" />
                                </v-btn>
                                <v-btn outlined fab color="deep-orange darken-4" @click.stop="onClickCancelAddEntry" class="xxs-btn">
                                    <font-awesome-icon icon="times" class="xxs-btnicon" />
                                </v-btn>
                            </div>
                        </div>
                    </v-container>
                </v-row>
                <div class="mainlistdata custom-scroll">
                    <v-row no-gutters v-for="entry in entriesList" :key="entry.id">
                        <v-container class="mb-n3">
                            <v-hover>
                            <v-card :to="'/splitter/' + entry.id" outlined shaped tile>
                                <v-list-item-title class="text-left mx-4 my-4 indigo--text">
                                    <div class="d-flex">
                                        <div class="mr-auto">{{ entry.title }}</div>
                                        <div><small class="grey--text lighten-4" v-if="entry.createdOn">{{ formatDate(entry.createdOn)}}</small></div>
                                    </div>
                                </v-list-item-title>
                            </v-card>
                            </v-hover>
                        </v-container>
                    </v-row>
                </div>
            </v-tab-item>
            <v-tab-item :key="1">
                <v-row no-gutters class="">
                    <v-container v-show="!showEditInputItem">
                        <v-btn outlined block color="indigo" class="" @click="onToggleEditInputItem(showEditInputItem)">
                            <font-awesome-icon icon="plus" size="sm" class="mr-1" /> ADD ITEM GROUP
                        </v-btn>
                    </v-container>
                    <v-container class="mb-n8 mt-2 " flat v-show="showEditInputItem">
                         <div class="d-flex">
                            <div class="flex-grow-1">
                                <v-text-field ref="itemName" label="Name" v-model="newItemName" @keydown.enter="storeItem" class="mr-3"></v-text-field>
                            </div>
                            <div class="mt-2">
                                <v-btn outlined fab color="indigo" @click.stop="storeItem" class="xxs-btn mr-1">
                                    <font-awesome-icon icon="check" class="xxs-btnicon" />
                                </v-btn>
                                <v-btn outlined fab color="deep-orange darken-4" @click.stop="onClickCancelAddItem" class="xxs-btn">
                                    <font-awesome-icon icon="times" class="xxs-btnicon" />
                                </v-btn>
                            </div>
                        </div> 
                    </v-container>
                </v-row>
                <div class="mainlistdata custom-scroll mr-3" v-if="itemList && itemList.length">  
                        <v-treeview :items="itemList" class="text-left " outlined shaped tile>
                            <template slot="append" slot-scope="{ item }"> 
                                <v-btn class="float-right white--text" color="red darken-3" v-if="item.children" fab elevation="0" x-small @click="removeParentEntity(item, 'item')">
                                    <font-awesome-icon icon="times" class="xxs-btnicon" /> 
                                </v-btn>

                                <v-btn class="float-right white--text mr-1" color="blue darken-3" v-if="item.children" fab elevation="0" x-small @click="editItemParent(item)">
                                    <font-awesome-icon icon="pencil-alt" class="xxs-btnicon" />
                                </v-btn> 

                                <v-btn class="float-right white--text mr-1" color="blue lighten-1" v-if="item.children" fab elevation="0" x-small @click="activateItemModal(item)">
                                    <font-awesome-icon icon="plus" class="xxs-btnicon" />
                                </v-btn>  

                                <v-btn class="float-right white--text" color="red darken-3" v-if="!item.children" fab elevation="0" x-small @click="removeChild(item)">
                                    <font-awesome-icon icon="times" class="xxs-btnicon" />
                                </v-btn> 
                                
                                <v-btn class="float-right white--text mr-1" color="blue darken-3" v-if="!item.children" fab elevation="0" x-small @click="activateItemModal(item)">
                                    <font-awesome-icon icon="pencil-alt" class="xxs-btnicon" />
                                </v-btn> 

                                <v-chip class="float-right mr-2 mt-1" v-if="!item.children" color="green" text-color="white" small>₱{{item.itemAmount}}</v-chip>
                            </template>
                         </v-treeview>
                         <ItemAddItemChild :activeModalItemObj="activeModalItemObj" :editMode="editItemMode" :showItemDialog="itemDialog" @activateDialog="activateItemModal" @addItemChildtoEntity="addItemChildtoEntity"></ItemAddItemChild>
                  
                </div>
            </v-tab-item>
            <v-tab-item :key="2">
                <v-row no-gutters class="">
                    <v-container v-show="!showEditInputMember">
                        <v-btn outlined block color="indigo" class="" @click="onToggleEditInputMember(showEditInputMember)">
                            <font-awesome-icon icon="plus" size="sm" class="mr-1" /> ADD MEMBER GROUP
                        </v-btn>
                    </v-container>
                    <v-container class="mb-n8 mt-2" v-show="showEditInputMember">
                        <div class="d-flex">
                            <div class="flex-grow-1">
                                <v-text-field ref="memberName" label="Name" v-model="newMemberName" @keydown.enter="storeMember" class="mr-3"></v-text-field>
                            </div>
                            <div class="mt-2">
                                <v-btn outlined fab color="indigo" @click.stop="storeMember" class="xxs-btn mr-1">
                                    <font-awesome-icon icon="check" class="xxs-btnicon" />
                                </v-btn>
                                <v-btn outlined fab color="deep-orange darken-4" @click.stop="onClickCancelAddMember" class="xxs-btn">
                                    <font-awesome-icon icon="times" class="xxs-btnicon" />
                                </v-btn>
                            </div>
                        </div>
                    </v-container>
                </v-row>
                <div class="mainlistdata custom-scroll mr-3" v-if="memberList && memberList.length"> 
                     
                            <v-treeview :items="memberList" class="text-left" outlined shaped tile>
                                <template slot="append" slot-scope="{ item }"> 
                                    <v-btn class="float-right white--text" color="red darken-3" v-if="item.children" fab elevation="0" x-small @click="removeParentEntity(item)">
                                            <font-awesome-icon icon="times" class="xxs-btnicon" />
                                        </v-btn>
                                        <v-btn class="float-right white--text mr-1" color="blue darken-3" v-if="item.children" fab elevation="0" x-small @click="activateMemberModal(item)">
                                            <font-awesome-icon icon="pencil-alt" class="xxs-btnicon" />
                                        </v-btn> 
                                        <v-btn class="float-right white--text mr-1" color="blue lighten-1" v-if="item.children" fab elevation="0" x-small @click="activateMemberModal(item)">
                                            <font-awesome-icon icon="plus" class="xxs-btnicon" />
                                        </v-btn>  
                                        <v-btn class="float-right white--text" color="red darken-3" v-if="!item.children" fab elevation="0" x-small @click="removeMemberChild(item)">
                                            <font-awesome-icon icon="times" class="xxs-btnicon" />
                                        </v-btn> 
                                        
                                        <v-btn class="float-right white--text mr-1" color="blue darken-3" v-if="!item.children" fab elevation="0" x-small @click="activateMemberModal(item)">
                                            <font-awesome-icon icon="pencil-alt" class="xxs-btnicon" />
                                        </v-btn>  
                                </template>  
                         </v-treeview>  
                        <MemberAddItemChild :activeModalMemberObj="activeModalMemberObj" :editMode="editMemberMode" :showItemDialog="memberDialog" @activateDialog="activateMemberModal" @addMemberChildtoEntity="addMemberChildtoEntity"></MemberAddItemChild>  
                </div>
            </v-tab-item>
        </v-tabs-items>
        <ConfirmModal ref="confirm"></ConfirmModal>
    </div>
</template>

<script>
    import {
        db
    } from '../main'
    import mixin from "../plugins/mixin";
    import firebase from 'firebase/app';
    import 'firebase/auth';
    import ItemAddItemChild from "../components/ItemAddItemChild.vue"
    import MemberAddItemChild from "../components/MemberAddItemChild.vue"
    import ConfirmModal from "../components/ConfirmModal.vue"
    export default {
        data: () => ({
            loggedInUser: "",
            showEditInputEntry: false,
            showEditInputItem: false,
            showEditInputMember: false,
            itemDialog: false,
            memberDialog: false,
            saveMembersModal: false,
            editItemMode: false,
            editMemberMode: false,
            editItemParentMode: false,
            editMemberParentMode: false,
            newEntryName: "",
            newEntryId: "",
            newItemName: "",
            newItemAmount: "",
            newItemId: "",
            newMemberName: "",
            newMemberId: "",
            tab: null,
            entriesList: [],
            itemList: [],
            memberList: [],
            activeDialogData: [],
            activeModalItemObj: {},
            activeModalMemberObj: {},
            activeItemParent: {},
            activeMemberParent: {},
            nextId: 1000
        }),
        mixins: [mixin],
        components: {
            ItemAddItemChild,
            MemberAddItemChild,
            ConfirmModal
        },
        created: function() {
            this.loggedInUser = firebase.auth().currentUser;
            if (this.loggedInUser) {
                this.fetchEntries();
                this.fetchItems();
                this.fetchMembers();
            }
        },
        methods: {
            async fetchEntries() {
                await db.collection('userData').doc(this.loggedInUser.uid).collection('entries').get()
                    .then(snapshot => {
                        this.entriesList = [];
                        snapshot.forEach(doc => {
                            const entry = doc.data();
                            entry.id = doc.id;
                            this.entriesList.push(entry);
                        })
                    })
                    .catch(error => {
                        console.error(error)
                    })
            },
            async fetchItems(callback) {
                await db.collection('userData').doc(this.loggedInUser.uid).collection('itemGroups').get()
                    .then(snapshot => {
                        this.itemList = [];
                        snapshot.forEach(doc => {
                            const entry = doc.data();
                            entry.id = doc.id;
                            this.itemList.push(entry);
                        });
                        
                        this.$nextTick(() => {
                            if (callback) callback();
                        })
                    })
                    .catch(error => {
                        console.error(error)
                    })
            },
            async fetchMembers(callback) {
                await db.collection('userData').doc(this.loggedInUser.uid).collection('memberGroups').get()
                    .then(snapshot => {
                        this.memberList = [];
                        snapshot.forEach(doc => {
                            const entry = doc.data();
                            entry.id = doc.id;
                            this.memberList.push(entry);
                        });
                        this.$nextTick(() => {
                            if (callback) callback();
                        });
                    })
                    .catch(error => {
                        console.error(error)
                    })
            },
            storeEntry: function() {
                let createdOnDate = firebase.firestore.FieldValue.serverTimestamp();
                db.collection('userData').doc(this.loggedInUser.uid).collection('entries').add({
                    title: this.newEntryName,
                    createdOn: createdOnDate
                }).then((docRef) => {
                    this.newEntryId = docRef.id;
                    db.collection("entries").doc(docRef.id).set({
                        title: this.newEntryName,
                        createdOn: createdOnDate,
                        ownerId: this.loggedInUser.uid,
                        items: [],
                        members: []
                    }).then(() => {
                        this.newEntryName = '';
                        this.$router.push({
                            path: `/splitter/${this.newEntryId}`
                        })
                    }).catch((error) => {
                        console.error("Error writing document: ", error);
                    });
                }).catch((error) => {
                    console.error("Error adding document: ", error);
                });
            },
            storeItem: function() {
                let self = this;
                if (this.editItemParentMode){
                    let requestData = { name: this.newItemName };
                    this.updateSubCollectionData({
                        collection: "userData",
                        docId: this.loggedInUser.uid,
                        subcollection: "itemGroups",
                        subcollectionDocId: this.activeItemParent.id,
                        requestData: requestData,
                        callback: function() {
                            self.fetchItems(function(){ 
                                self.editItemParent(self.activeItemParent, false);
                            });
                        }
                    });
                }else{ 
                    let createdOnDate = firebase.firestore.FieldValue.serverTimestamp();
                    db.collection('userData').doc(this.loggedInUser.uid).collection('itemGroups').add({
                        name: this.newItemName,
                        createdOn: createdOnDate,
                        children: []
                    }).then(() => {
                        self.newItemName = "";
                        self.showEditInputItem = false;
                        self.fetchItems(); 
                    }).catch((error) => {
                        console.error("Error adding document: ", error);
                    });
                }
            },
            storeMember: function() {
                let self = this;
                if (this.editMemberParentMode){
                    let requestData = { name: this.newMemberName };
                    this.updateSubCollectionData({
                        collection: "userData",
                        docId: this.loggedInUser.uid,
                        subcollection: "memberGroups",
                        subcollectionDocId: this.activeMemberParent.id,
                        requestData: requestData,
                        callback: function() {
                            self.fetchMembers(function(){
                                self.setActiveMemberParent(self.activeMemberParent);  
                            });
                        }
                    });
                }else{
                    let createdOnDate = firebase.firestore.FieldValue.serverTimestamp();
                    db.collection('userData').doc(this.loggedInUser.uid).collection('memberGroups').add({
                        name: this.newMemberName,
                        createdOn: createdOnDate,
                        children: []
                    }).then(() => { 
                        self.newMemberName = "";
                        self.showEditInputMember = false;
                        self.fetchMembers();  
                    }).catch((error) => {
                        console.error("Error adding document: ", error);
                    }); 
                }
            },
            onClickCancelAddEntry: function() {
                this.newEntryName = '';
                this.showEditInputEntry = false;
            },
            onClickCancelAddItem: function() {
                this.newItemName = '';
                this.showEditInputItem = false;
            },
            onClickCancelAddMember: function() {
                this.newMemberName = '';
                this.showEditInputMember = false;
            },
            onToggleEditInputEntry(showEditItem) {
                this.showEditInputEntry = !showEditItem;
                if (this.showEditInputEntry) {
                    this.$nextTick(() => {
                        this.$refs.entryName.focus()
                    })
                }
            },
            onToggleEditInputItem(showEditItem) {
                this.showEditInputItem = !showEditItem;
                if (this.showEditInputItem) {
                    this.$nextTick(() => {
                        this.$refs.itemName.focus()
                    })
                }
            },
            onToggleEditInputMember(showEditItem) {
                this.showEditInputMember = !showEditItem;
                if (this.showEditInputMember) {
                    this.$nextTick(() => {
                        this.$refs.memberName.focus()
                    })
                }
            }, 
            editItemParent(item, editMode) {  
                editMode = typeof editMode === "undefined" ? true : editMode;
                this.activeItemParent = item;
                this.newItemName = editMode ? item.name : "";
                this.editItemParentMode = editMode;
                this.onToggleEditInputItem(!editMode);
            },
            setActiveItemParent(item){ 
                let findItem = {}, itemChildren = this.itemList;
                if (!item.children){ 
                    findItem = itemChildren.find(itemChildren => itemChildren.children.some(el => el.id === item.id));
                    if (findItem) this.activeItemParent = findItem; 
                }else{
                    this.activeItemParent = itemChildren.find(x => x.id === item.id);  
                } 
            },
            setActiveMemberParent(member){ 
                let findMember = {}, memberChildren = this.memberList;
                if (!member.children){ 
                    findMember = memberChildren.find(memberChildren => memberChildren.children.some(el => el.id === member.id));
                    if (findMember) this.activeMemberParent = findMember; 
                }else{
                    this.activeMemberParent = memberChildren.find(x => x.id === member.id);  
                } 
            },
            activateItemModal(item) {
                let resetMode = typeof item.reset !== "undefined";
                this.editItemMode = resetMode ? !resetMode : typeof item.children === "undefined";
                this.activeModalItemObj = resetMode ? {} : item;
                this.itemDialog = resetMode ? !item.reset : true;
                if (item.id) this.setActiveItemParent(item);
            },
            activateMemberModal(member) {
                let resetMode = typeof member.reset !== "undefined";
                this.editMemberMode = resetMode ? !resetMode : typeof member.children === "undefined";
                this.activeModalMemberObj = resetMode ? {} : member;
                this.memberDialog = resetMode ? !member.reset : true;
                if (member.id) this.setActiveMemberParent(member);
            },
            addItemChildtoEntity(item) {
                if (!item.persistWindow) this.itemDialog = !this.itemDialog;
                let self = this, closeModal = false;
                let requestData = {};
                if (item.editMode) {
                    let findChildToEdit = this.activeItemParent.children.find(x => x.id === item.id);
                    if (findChildToEdit) {
                        findChildToEdit.name = item.newItemName;
                        findChildToEdit.itemAmount = parseFloat(item.newItemAmount).toFixed(2);
                    }
                    requestData = {
                        children: this.activeItemParent.children
                    };
                } else { 
                    requestData = {
                        children: [{
                                id: this.getUniqueID(),
                                name: item.newItemName,
                                itemAmount: parseFloat(item.newItemAmount).toFixed(2)
                            },
                            ...this.activeItemParent.children
                        ]
                    };
                }
                this.updateSubCollectionData({
                    collection: "userData",
                    docId: this.loggedInUser.uid,
                    subcollection: "itemGroups",
                    subcollectionDocId: this.activeItemParent.id,
                    requestData: requestData,
                    callback: function() {
                        self.fetchItems(function(){
                            self.setActiveItemParent(self.activeItemParent);  
                        });
                    }
                });
                if (item.editMode || closeModal) {
                    this.activateItemModal({
                        reset: true
                    });
                } 
            }, 
            addMemberChildtoEntity(member) {
                if (!member.persistWindow) this.memberDialog = !this.memberDialog;
                let self = this, closeModal = false;
                let requestData = {};
                if (member.editMode) {
                    let findChildToEdit = this.activeMemberParent.children.find(x => x.id === member.id);
                    if (findChildToEdit) {
                        findChildToEdit.name = member.newMemberName; 
                    }
                    requestData = {
                        children: this.activeMemberParent.children
                    };
                } else { 
                    requestData = {
                        children: [{
                                id: this.getUniqueID(),
                                name: member.newMemberName 
                            },
                            ...this.activeMemberParent.children
                        ]
                    };
                }
                this.updateSubCollectionData({
                    collection: "userData",
                    docId: this.loggedInUser.uid,
                    subcollection: "memberGroups",
                    subcollectionDocId: this.activeMemberParent.id,
                    requestData: requestData,
                    callback: function() {
                        self.fetchMembers(function(){
                            self.setActiveMemberParent(self.activeMemberParent);  
                        });
                    }
                });
                if (member.editMode || closeModal) {
                    this.activateMemberModal({
                        reset: true
                    });
                } 
            }, 
            removeParentEntity(item, type){  
                let entity = item, itemMode = type === "item", subCollectionType = itemMode ?  "itemGroups" : "memberGroups";
                this.$refs.confirm.open({title: 'Delete', message: `Remove ${entity.name} and its contents?`, options: {color: 'red'} }).then((confirm) => {
                    if (confirm){
                        let self = this;
                        this.deleteSubCollectionData({
                            collection: "userData",
                            docId: this.loggedInUser.uid,
                            subcollection: subCollectionType,
                            subcollectionDocId: entity.id, 
                            callback: function() {
                                itemMode ? self.fetchItems() : self.fetchMembers();
                            }
                        });  
                    }
                });  
            },
            removeChild(item){ 
                if (item.id) this.setActiveItemParent(item);
                this.$refs.confirm.open({title: 'Delete', message: `Remove ${item.name}?`, options: {color: 'red'} }).then((confirm) => {
                 
                    if (confirm){
                        let requestData = {
                            children: this.activeItemParent.children.filter(x=> x.id !== item.id)
                        }, self = this;
                        this.updateSubCollectionData({
                            collection: "userData",
                            docId: this.loggedInUser.uid,
                            subcollection: "itemGroups",
                            subcollectionDocId: this.activeItemParent.id,
                            requestData: requestData,
                            callback: function() {
                                self.fetchItems();
                            }
                        });  
                    }
                }); 
            },
            removeMemberChild(member){ 
                if (member.id) this.setActiveMemberParent(member);
                this.$refs.confirm.open({title: 'Delete', message: `Remove ${member.name}?`, options: {color: 'red'} }).then((confirm) => {
                    
                    if (confirm){
                        let requestData = {
                            children: this.activeMemberParent.children.filter(x=> x.id !== member.id)
                        }, self = this;
                        this.updateSubCollectionData({
                            collection: "userData",
                            docId: this.loggedInUser.uid,
                            subcollection: "memberGroups",
                            subcollectionDocId: this.activeMemberParent.id,
                            requestData: requestData,
                            callback: function() {
                                self.fetchMembers();
                            }
                        });  
                    }
                }); 
            },
        }
    }
</script>
   
<style>
.v-treeview-node__label {
    white-space: normal!important;
}
</style>