<template>  
    <v-card class="mx-auto mt-2 custom-scroll overflow-y-auto listdata" flat > 
        <div class="overflow-x-hidden overflow-y-hidden " ref="printcontent"> 
      <v-row no-gutters v-for="item in listdata" :key="item.id">
      <v-col class="col-12">
          <v-divider></v-divider>
          <v-expansion-panels accordion flat>
            <v-expansion-panel>
              <v-expansion-panel-header @click.native="expansionPanel()" class="px-2 py-0 mb-n2">
                <v-col class="col-3 flex-column d-flex">
                  <div class="ml-n3"> <b>{{item.name}} </b> </div>
                </v-col>
                <v-col class="col-2 flex-column d-flex">
                  <small class="ml-n3">₱{{ item.itemAmount }}</small>
                </v-col>
                <v-col class="col-1 flex-column d-flex">
                  <small class="ml-n3" v-show="item.itemTotalQty">*</small>
                </v-col>
                <v-col class="col-2 flex-column d-flex">
                  <small class="ml-n3" v-show="item.itemTotalQty">{{ item.itemTotalQty }}</small>
                </v-col>
                <v-col class="col-amount col-3 flex-column d-flex text-right">
                  <b class="text-right indigo--text" v-show="item.memberList.length">₱{{ item.itemTotal }}</b>
                </v-col>

              </v-expansion-panel-header>
              <v-card-subtitle class="row-item pl-2 py-0 mt-1 mb-2">
                <div class="d-flex"> 
                  <div class="card-sub d-flex justify-start  mr-auto">
                    <v-btn outlined fab color="indigo" data-html2canvas-ignore="true" :disabled="!ownerHasLoggedIn" @click.stop="onClickAddPersonToItem(item)" class="xxs-btn dashed ">
                      <font-awesome-icon icon="plus" class="xxs-btnicon" />
                    </v-btn> 
                      <div class="d-flex justify-start"> 
                        <div class="" v-for="child in item.memberList.slice(0,3)" :key="child.id">
                        <v-avatar class="ml-1 d-flex" color="blue " size="22"> 
                  <div class="white--text xxs-fs" :title="`${displayName(child, {name: true})}`">
                    {{ displayName(child, {initials: true}) }}</div> 
                    </v-avatar>
                      </div>
                      <div v-if="item.memberList.length > 3">
                        <v-avatar class="ml-1 d-flex" color="blue  lighten-2" size="22"> 
                  <div class="white--text xxs-fs">
                    +{{ item.memberList.length - 3 }}</div> 
                    </v-avatar>
                      </div>
                      </div> 
                      
                  </div>
                  <div class="card-sub ">  
                     <v-chip v-show="item.isItemShared" class="mr-6 green darken-1V" label x-small text-color="white">SHARED</v-chip> 
                    <v-menu offset-y >
        <template v-slot:activator="{ on, attrs }">
          <v-btn :disabled="!ownerHasLoggedIn" data-html2canvas-ignore="true"
            outlined color="indigo"  x-small
            v-bind="attrs"
            v-on="on"
          >
              <font-awesome-icon icon="cog" />  
          </v-btn>
        </template>
        <v-list>
          <v-list-item @click="onClickEditItem(item)" :dense="true">
            <v-list-item-title class="text-left indigo--text"> <font-awesome-icon icon="pencil-alt" class="mr-1" />  Edit</v-list-item-title>
          </v-list-item>
          <v-list-item @click="onClickDeleteItem(item)"  :dense="true" >
            <v-list-item-title class="text-left red--text" > <font-awesome-icon icon="trash-alt" class="mr-1" />  Remove</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
                  </div>  
                </div>
              </v-card-subtitle>
              <v-expansion-panel-content class="childrenlist" color="grey lighten-4">
                <v-list-item v-for="child in item.memberList" :key="child.id" class="ml-n8">
                  <v-list-item-content>
                    <div class="d-flex item-children">
                      <div class="text-left "><small v-text="child.name" ></small></div>
                      <div class="text-left">₱<small v-text="child.itemAmount"></small></div>
                      <div class="text-left" ><small v-show="item.isItemShared">/</small><small v-show="!item.isItemShared">*</small></div>
                      <div class="text-left "><small v-text="child.itemQty"></small></div>
                      <div class=""> <b class="text-right" > ₱<span v-text="child.itemTotal"></span></b></div> 
                    </div> 
                  </v-list-item-content>
                </v-list-item>
                <div class="mt-3" v-show="!item.memberList.length" ><small>No members added</small></div> 
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-col>

      </v-row>
    </div>
    </v-card>  
</template>

<script>
// import mixin from "../plugins/mixin";
  export default {
    name: "ItemList",
    data() {
      return {
        checkbox: [] 
      };
    },
    // mixins: [mixin],
    props: {
      listdata: {
        type: Array,
      },
      drawerValue: {
        type: Boolean,
      },
      ownerHasLoggedIn: {
        type: Boolean,
      },
    },
    methods: {
      displayName: function(obj, dataoptions) { 
            const { name } = obj;
      if (dataoptions.initials) return  name.charAt(0) + name.charAt(1).toUpperCase();
      if (dataoptions.name) return name;  
      },
      expansionPanel: function() { 
      },
      check: function(e) {
        e.preventDefault();
        e.stopImmediatePropagation(); 
      },
      onClickAddPersonToItem: function(item) { 
        this.$emit("setActiveItem", item);
        this.$emit("activatePersonBtmDrawer", !this.drawerValue);
      },
      onClickEditItem: function(item) {  
        this.$emit("activateEditItem", item);
      },
      onClickDeleteItem: function(item) {   
        this.$emit("activateDeleteItem", item);
      },
      getSumofItems(listData, fieldName) {
        let aggregatedVal = 0;
        for (let listItem of listData) {
          aggregatedVal = (parseFloat(aggregatedVal) + parseFloat(listItem[fieldName])).toFixed(2);
        }
        return aggregatedVal;
      },
      calculateQtyTotal: function(amount, qty) {   
        return parseFloat(parseFloat(amount) * parseInt(qty)).toFixed(2);
      },
    },
  };
</script>
