
<template>
  <v-dialog @input="closeModal()" v-model="displayDialog" @keydown.esc="closeModal" max-width="500px">
    <v-card> 
      <v-card-text style="height: 100px;">
        <v-container> 
           <v-layout class="pa-1">
                <v-flex x12>
                    <div class="pa-1">
                        <v-text-field ref="memberName" label="Name" v-model="newMemberName" @keydown.enter="saveEntity"></v-text-field>
                    </div>
                </v-flex>  
            </v-layout>
        </v-container>
      </v-card-text>
     
      <v-divider></v-divider>
      <v-card-actions class="d-flex">
        <div class="mr-auto"></div>
        <div>
        <v-btn color="blue darken-1" text @click="saveEntity" v-show="!editMode">
          Save and add new
        </v-btn>
        <v-btn color="blue darken-1" text @click="saveAndClose">
          Save and close
        </v-btn>
        </div>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>  
<script> 
   export default {
     data: () => ({ 
       displayDialog: false, 
       showEditInputItem: false, 
       inputItemMode: false, 
        editItemData: {},
        editId: 0, 
        newMemberName: "" 
    }), 
     props: {
       showItemDialog: {
         type: Boolean,
         default: false
       }, 
       editMode: {type: Boolean},
       activeModalMemberObj: {type: Object},
     },  
     watch:{
       showItemDialog(newValue){
         this.displayDialog = newValue; 
       },
       editMode(newValue){
         if (newValue && this.activeModalMemberObj){
            this.editId = this.activeModalMemberObj.id;
            this.newMemberName = this.activeModalMemberObj.name; 
         }
       },
       displayDialog(){
         setTimeout(() => {
          if (this.displayDialog){ 
              this.$nextTick(() => {
                  this.$refs.memberName.focus() 
              }) 
          } 
         }, 200);
       }
     },
     methods: { 
       closeModal() {  
          this.$emit('activateDialog', {name: "memberDialog", value: false, dataName: "memberDialogData", data: [], reset: true});
          this.resetValues();
       } ,
       saveCheckedItems() {   
          this.closeModal(); 
       } ,
      onToggleEditInputItem(showEditItem) {
        this.showEditInputItem = !showEditItem;
        if (!this.showEditInputItem) this.inputItemMode = false;
      }, 
      saveAndClose() { 
        this.saveEntity({closewindow: true});
      }, 
      saveEntity(dataOptions){ 
         if (this.newMemberName){ 
           let childDataOptions = { 
            newMemberName: this.newMemberName,  
            persistWindow: !dataOptions.closewindow, editMode: this.editMode };
          if (this.editMode) childDataOptions.id = this.editId;
          this.$emit("addMemberChildtoEntity", childDataOptions);  
          this.resetValues();
        }
      }, 
      resetValues(){  
          this.newMemberName = ''; 
          this.editId = 0;
          this.$refs.memberName.focus(); 
      },
     }
   }
</script>