import Vue from "vue";
import VueRouter from "vue-router";
import Splitter from "../views/Splitter.vue";
import Entries from "../views/Entries.vue"; 
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Entries",
    component: Entries,
    meta: { requiresAuth: true }, 
  },
  {
    path: "/splitter/:id",
    name: "Splitter",
    component: Splitter, 
  } 
];

const router = new VueRouter({
  routes,
});

export default router;
