
<template>
  <v-dialog @input="closeModal()" v-model="displayDialog" @keydown.esc="closeModal" max-width="500px">
    <v-card> 
      <v-card-text style="height: 100px;">
        <v-container> 
           <v-layout class="pa-1">
                <v-flex xs8>
                    <div class="pa-1">
                        <v-text-field ref="itemName" label="Name" v-model="newItemName" @keydown.enter="saveEntity"></v-text-field>
                    </div>
                </v-flex>
                <v-flex xs4 >
                    <div class="pa-1 right-input">
                        <v-text-field ref="itemAmount" type="number" label="Amount" prefix="₱"  v-model="newItemAmount" @keydown.enter="saveEntity">
                        </v-text-field>
                    </div>
                </v-flex>  
            </v-layout>
        </v-container>
      </v-card-text>
     
      <v-divider></v-divider>
      <v-card-actions class="d-flex">
        <div class="mr-auto"></div>
        <div>
        <v-btn color="blue darken-1" text @click="saveEntity" v-show="!editMode">
          Save and add new
        </v-btn>
        <v-btn color="blue darken-1" text @click="saveAndClose">
          Save and close
        </v-btn>
        </div>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>  
<script> 
   export default {
     data: () => ({ 
       displayDialog: false, 
       showEditInputItem: false, 
       inputItemMode: false, 
        editItemData: {},
        editId: 0, 
        newItemName: "", 
        newItemAmount: "" 
    }), 
     props: {
       showItemDialog: {
         type: Boolean,
         default: false
       }, 
       editMode: {type: Boolean},
       activeModalItemObj: {type: Object},
     },  
     watch:{
       showItemDialog(newValue){
         this.displayDialog = newValue; 
       },
       editMode(newValue){
         if (newValue && this.activeModalItemObj){
            this.editId = this.activeModalItemObj.id;
            this.newItemName = this.activeModalItemObj.name;
            this.newItemAmount = this.activeModalItemObj.itemAmount;
         }
       },
       displayDialog(){
         setTimeout(() => {
          if (this.displayDialog){ 
              this.$nextTick(() => {
                  this.$refs.itemName.focus() 
              }) 
          } 
         }, 200);
       }
     },
     methods: { 
       closeModal() {  
          this.$emit('activateDialog', {name: "itemDialog", value: false, dataName: "itemDialogData", data: [], reset: true});
          this.resetValues();
       } ,
       saveCheckedItems() {   
          this.closeModal(); 
       } ,
      onToggleEditInputItem(showEditItem) {
        this.showEditInputItem = !showEditItem;
        if (!this.showEditInputItem) this.inputItemMode = false;
      }, 
      saveAndClose() { 
        this.saveEntity({closewindow: true});
      }, 
      saveEntity(dataOptions){ 
         if (this.newItemName && this.newItemAmount){ 
           let childDataOptions = { 
            newItemName: this.newItemName, 
            newItemAmount: parseFloat(this.newItemAmount).toFixed(2), 
            persistWindow: !dataOptions.closewindow, editMode: this.editMode };
            if (this.editMode) childDataOptions.id = this.editId;
          this.$emit("addItemChildtoEntity", childDataOptions);  
          this.resetValues();
        }
      }, 
      resetValues(){  
          this.newItemName = '';
          this.newItemAmount = '';
          this.editId = 0;
          this.$refs.itemName.focus(); 
      },
     }
   }
</script>