
<template>
  <v-dialog @input="closeModal()" v-model="displayDialog" @keydown.esc="closeModal" max-width="500px">
    <v-card> 
      <v-card-text class="custom-scroll overflow-y-auto pl-0" style="height: 400px;">
        <v-container>
            <v-treeview :items="importDialogData" class="text-left" selectable v-model="selection"> 
                
            </v-treeview>
        </v-container>
      </v-card-text>
     
      <v-divider></v-divider>
      <v-card-actions class="d-flex">
        <div class="mr-auto"></div>
        <div>
        <v-btn color="blue darken-1" text @click="saveCheckedItems">
          Add
        </v-btn></div>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>  
<script>
   export default {
     data: () => ({ 
       displayDialog: false,
       selection: [] 
    }),
     props: {
       showImportDialog: {
         type: Boolean,
         default: false
       },
       importDialogData: {
         type: Array
       }
     }, 
     watch:{
       showImportDialog(newValue){
         this.displayDialog = newValue; 
       }, 
     },
     methods: { 
       closeModal() {  
          this.$emit('activateDialog', {dataName: "memberDialogData", name: "memberDialog", data: [], value: false });
       } ,
       saveCheckedItems() {    
          this.closeModal();
          this.$emit("importMembers", {selected: this.selection, list: this.importDialogData});
       } 
     }
   }
</script>