
<template>
  <v-dialog @input="closeModal()" v-model="displayDialog" @keydown.esc="closeModal" max-width="500px">
    <v-card >  
       <v-list>
         <div class="ml-6 mr-6">
            <v-text-field label="Group Name" v-model="newGroupName">
                        </v-text-field>
         </div>
      <v-list-item v-for="item in addMembersList" :key="item.id">
                    <v-list-item-subtitle >
                        <v-checkbox v-model="item.isChecked" dense  
                        :label="`${item.name}`" 
                        class="ml-2" > </v-checkbox>  
                    </v-list-item-subtitle>
                </v-list-item>
      </v-list>
      <v-divider></v-divider>
      <v-card-actions class="d-flex">
        <div class="mr-auto"></div>
        <div>
        <v-btn color="blue darken-1" text @click="saveCheckedItems">
          Add
        </v-btn></div>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>  
<script>
   export default {
     data: () => ({ 
       displayDialog: false,
       addMembersList: [],
       newGroupName: "" 
    }),
     props: {
       showSaveMembersModal: {
         type: Boolean,
         default: false
       },
       membersList: {
         type: Array
       }
     }, 
     watch:{
       showSaveMembersModal(newValue){
         this.displayDialog = newValue;  
       }, 
       membersList(newList){
         this.addMembersList = newList.map((member) => { 
           return { id: member.id, name: member.name, isChecked: true };
         }); 
       }, 
     },
     methods: { 
       closeModal() {  
         this.newGroupName = "";
        // this.addMembersList = [];
          this.$emit('showAddMembersToGroup', { hideModal: true });
       } , 
       saveCheckedItems() {    
         if (this.newGroupName){
           let finalMembersList = this.addMembersList.filter(x => x.isChecked);
          this.$emit("submitMembersToGroup", {children: finalMembersList, groupName: this.newGroupName});
          this.closeModal();
        } 
       } 
     }
   }
</script>