<template>
    <v-card class="mx-auto mt-2 custom-scroll overflow-y-auto listdata" flat> 
        <div class="overflow-x-hidden overflow-y-hidden " ref="printcontent">
            <v-row no-gutters v-for="person in listdata" :key="person.id">
                <v-col class="col-12">
                    <v-divider></v-divider>
                    <v-expansion-panels   flat  >
                        <v-expansion-panel>
                            <v-expansion-panel-header class="px-2 py-0 mb-n2" ref="expansionpanelitem">
                                <v-col class="col col-8 flex-column d-flex">
                                    <div class="ml-n3"> <b>{{person.name}} </b></div>
                                   
                                </v-col>
                                <v-col class="col col-3 flex-column d-flex">
                                    <b class="text-right indigo--text" v-if="person.totalPayable">₱{{person.totalPayable}}</b>
                                </v-col>
                            </v-expansion-panel-header>
                            <v-card-subtitle class="row-item pl-2 py-0 mt-1 mb-2 position-absolute">
                                <div class="d-flex">
                                    <div class="card-sub mr-auto">
                                        <v-btn :disabled="!ownerHasLoggedIn" data-html2canvas-ignore="true" outlined fab color="indigo" @click.stop="onClickAddItemToPerson(person)" class="xxs-btn dashed">
                                            <font-awesome-icon icon="plus" class="xxs-btnicon" />
                                        </v-btn>
                                    </div>
                                    <div class="card-sub ">
                                        <v-menu offset-y>
                                            <template v-slot:activator="{ on, attrs }">
                    <v-btn data-html2canvas-ignore="true"
                      :disabled="!ownerHasLoggedIn"
                      outlined color="indigo"  x-small
                      v-bind="attrs"
                      v-on="on"
                    >
                        <font-awesome-icon icon="cog" />  
                    </v-btn>
    </template>
    <v-list> 
        <!-- <v-list-item @click="onClickPay(person)" :dense="true">
        <v-list-item-title class="text-left green--text"> <font-awesome-icon icon="check" class="mr-1" />  Pay</v-list-item-title>
        </v-list-item> -->
        <v-list-item @click="onClickEditPerson(person)" :dense="true">
        <v-list-item-title class="text-left indigo--text"> <font-awesome-icon icon="pencil-alt" class="mr-1" />  Edit</v-list-item-title>
        </v-list-item>
        <v-list-item @click="onClickDeletePerson(person)"  :dense="true" >
        <v-list-item-title class="text-left red--text" > <font-awesome-icon icon="trash-alt" class="mr-1" />  Remove</v-list-item-title>
        </v-list-item>
    </v-list>
    </v-menu>
    </div> 
    </div>
    </v-card-subtitle>
    <v-expansion-panel-content class="childrenlist" color="grey lighten-4" >
    <v-list-item v-for="child in person.itemList" :key="child.id" class="ml-n8">
        <v-list-item-content>
            <div class="d-flex item-children">
                <div class="text-left"> <small class="text-left" v-text="child.name"></small></div>
                <div class="text-left"> ₱<small v-text="child.itemAmount"></small></div>
                <div class="text-left" v-show="!child.isItemShared">*</div>
                <div class="text-left" v-show="child.isItemShared">/</div>
                <div class="text-left"> <small v-text="child.itemQty"></small></div>
                <div class=""> <b class="text-right ">₱<span v-text="child.itemTotal"></span></b></div> 
            </div>
        </v-list-item-content>
        </v-list-item>
          <div class="mt-3" v-show="!person.itemList.length" ><small>No items added</small></div> 
    </v-expansion-panel-content>
    </v-expansion-panel>
    </v-expansion-panels>
    </v-col>
    </v-row>
    </div>
    </v-card>
    </template>

<script> 
    export default {
        name: "MemberList",
        data() {
            return {
                checkbox: []
            }
        },
        props: {
            listdata: {
                type: Array,
            },
            drawerValue: {
                type: Boolean,
            },
            ownerHasLoggedIn: {
                type: Boolean,
            },
        },
        methods: {
            calculateTotal: function(list) {
                let userTotalPayable = 0;
                for (let personsItem of list) {
                    userTotalPayable = (parseFloat(userTotalPayable) + (parseFloat(personsItem.amount) * personsItem.itemQty)).toFixed(2);
                }
                return userTotalPayable;
            },
            onClickPay: function(person) { 
                this.$emit("activatePayment", person);
            },
            onClickAddItemToPerson: function(person) {
                this.$emit('activateItemBtmDrawer', !this.drawerValue);
                this.$emit('setActivePerson', person);
            },
            onClickEditPerson: function(person) {
                this.$emit("activateEditPerson", person);
            },
            onClickDeletePerson: function(person) {
                this.$emit("activateDeletePerson", person);
            },
            getSumofItems(listData, fieldName) {
                let aggregatedVal = 0;
                for (let listItem of listData) {
                    aggregatedVal = (parseFloat(aggregatedVal) + parseFloat(listItem[fieldName])).toFixed(2);
                }
                return aggregatedVal;
            }
        }
    }
</script>
