<template>
    <div class="">
<v-container class="pa-0 mb-n6 mt-n3" flat>
            <v-layout class="pa-1">
                <v-flex xs12>
                    <div class="pa-1">
                         <v-text-field ref="personName" label="Name" v-model="newPersonName" @keydown.enter="storePerson" @keydown.esc="onClickCancelAddPerson"></v-text-field>
                    </div>
                </v-flex> 
                <v-flex xs1 class="actions-col">
                    <div class="pa-1 text-right mt-5 mr-1 pos-abs r-0"> 
                         <v-btn outlined fab color="deep-orange darken-4" @click.stop="onClickCancelAddPerson" class="xxs-btn">
                            <font-awesome-icon icon="times" class="xxs-btnicon" />
                        </v-btn>
                    </div>
                </v-flex>
            </v-layout>
        </v-container>  
    </div>
</template>

<script>
    export default {
        data: () => ({
            editId: 0, 
            newPersonName: "",
        }),
         props: { 
            inputPersonMode: {
                type: Boolean
            },
            editPersonData: {
                type: Object
            }
        },
        watch: { 
            inputPersonMode(){
                this.$nextTick(() => {
                    this.inputPersonMode && this.$refs.personName.focus();
                    if (this.editPersonData){  
                        this.newPersonName = this.editPersonData.name;
                        this.editId = this.editPersonData.id; 
                    }
                })
            }
        }, 
        methods: {
            storePerson() {
                if (this.newPersonName) {
                    const newPerson = {
                        newPersonName: this.newPersonName 
                    };
                     if (this.editId) newPerson.id = this.editId;
                    this.$emit('saveEditPerson', newPerson);
                     this.resetValues();
                }
            }, 
            resetValues(){  
                this.newPersonName = ''; 
                this.editId = 0;
                this.$refs.personName.focus();
            },
            onClickCancelAddPerson(){
                    this.$emit('cancelAddPerson', this.inputPersonMode);
            } 
        }, 
    }
</script>
