<template>
    <div class="">
        <v-container class="pa-0 mb-n6 mt-n3" flat>
            <v-layout class="pa-1">
                <v-flex xs6>
                    <div class="pa-1">
                        <v-text-field ref="itemName" label="Name" v-model="newItemName" @keydown.enter="storeItem" @keydown.esc="onClickCancelAddItem"></v-text-field>
                    </div>
                </v-flex>
                <v-flex xs6>
                    <div class="pa-1 right-input">
                        <v-text-field ref="itemAmount" type="number" label="Amount" prefix="₱"  v-model="newItemAmount" @keydown.enter="storeItem" @keydown.esc="onClickCancelAddItem">
                        </v-text-field>
                    </div>
                </v-flex> 
                <v-flex xs1 class="actions-col">
                    <div class="pa-1 text-right mt-5 mr-1 pos-abs r-0">
                        <!-- <v-btn outlined fab color="indigo" @click.stop="storeItem" class="xxs-btn mr-1">
                            <font-awesome-icon icon="check" class="xxs-btnicon" />
                        </v-btn> -->
                        <v-btn outlined fab color="deep-orange darken-4" @click.stop="onClickCancelAddItem" class="xxs-btn">
                            <font-awesome-icon icon="times" class="xxs-btnicon" />
                        </v-btn>
                    </div>
                </v-flex>
            </v-layout>
        </v-container>
    </div>
</template>

<script>
    export default {
        data: () => ({
            editId: 0, 
            newItemName: "", 
            newItemAmount: "" 
        }),
        props: {
            inputItemMode: {
                type: Boolean
            },
            editItemData: {
                type: Object
            }
        },
        watch:{ 
            inputItemMode(){
                this.$nextTick(() => { 
                    this.inputItemMode && this.$refs.itemName.focus();
                    if (this.editItemData){ 
                        this.newItemAmount = this.editItemData.itemAmount;
                        this.newItemName = this.editItemData.name;
                        this.editId = this.editItemData.id; 
                    }
                }) 
            }
        },  
        methods: {
            storeItem() {
                if (this.newItemName && this.newItemAmount) {
                    const newItem = {
                        newItemName: this.newItemName, 
                        newItemAmount: this.newItemAmount 
                    };
                    if (this.editId) newItem.id = this.editId;
                    this.$emit('saveEditItem', newItem);
                     this.resetValues();
                } else {
                    if (!this.newItemAmount) this.$refs.itemAmount.focus();
                }
            },
            resetValues(){ 
                // this.newItemQty = '';
                this.newItemName = '';
                this.newItemAmount = '';
                this.editId = 0;
                this.$refs.itemName.focus();
            },
            onClickCancelAddItem() {
                this.$emit('cancelAddItem', this.inputItemMode);
            },
            // setQuantity() { 
            //     if (!this.newItemQty) {
            //         this.newItemQty = '1';
            //     }
            // }
        }
    }
</script>
