<template>
  <v-app id="inspire" class="">
    <v-app-bar app class="indigo darken-2">
      <v-toolbar-title class="white--text">Splitter</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn small outlined @click="signInUser" color="white" v-if="!user">
        Sign In
      </v-btn>
      <v-menu :nudge-bottom="40" v-if="user">
        <template v-slot:activator="{ on, attrs }">
        <v-btn icon x-large v-bind="attrs" v-on="on" >
                    <v-avatar color="brown" size="35" >
                        <img :src="`${user.photoURL}`"/> 
                    </v-avatar>
                  </v-btn>
      </template> 
      <v-list>
        <v-list-item @click="logoutUser" >
          <v-list-item-title>Logout</v-list-item-title>
        </v-list-item>
      </v-list>
      </v-menu> 
      </v-app-bar> 
      <v-main > 
      <router-view v-if="showPage" :key="componentKey"></router-view>    
      <v-alert class="mt-4" v-if="!this.user" color="white" >Please sign in</v-alert>
      </v-main>
  </v-app>
</template>

<script> 
  import firebase from 'firebase/app';
  import 'firebase/auth';  
 
  export default {
    data: () => ({
      user: "",
            componentKey: 0,
      showPage: false
    }),
    mounted() {
      this.user = firebase.auth().currentUser;
      const isMainPage = location.hash.substr(2, location.hash.length) === "";
      this.showPage = !isMainPage || (isMainPage && this.user !== null); 
     }, 
    methods: {
      signInUser() {
        const provider = new firebase.auth.GoogleAuthProvider();
        firebase.auth().signInWithPopup(provider).then(() => {
          this.user = firebase.auth().currentUser;  
          this.showPage = true;
        this.forceRerender();
        }).catch((err) => {
          alert('Oops. ' + err.message)
        });
      }, 
      forceRerender() {
          this.componentKey += 1;
      }, 
      logoutUser() {
        firebase.auth().signOut();
        this.user = null;
        this.forceRerender(); 
      }
    }
  }
</script>

<style>
  html {
    overflow: hidden !important;
  }
  .v-application {
    text-align: center;
    max-width: 750px;
    margin: 0 auto;
  }
</style>