<template>
  <div class="splitter-container overflow-hidden px-1">
    <div class="text-left">
      <div v-if="entryData" class="mt-3">
        <h3>
          <div class="d-flex">
            <div class="d-flex mr-auto ">
              <router-link to="/" v-if="loggedInUser">
                <font-awesome-icon icon="arrow-left" class="mr-2" />
              </router-link>
              <div>
                <div v-show="entryTitleEditMode">
                    <v-text-field ref="entryTitle" label="Title" v-model="newEntryTitle" @keydown.enter="saveEntryTitle">
                        </v-text-field>
                </div>
                <div v-show="!entryTitleEditMode">{{ entryData.title }}</div>  
              </div>
            </div>  
            <div class="mr-3">
              <v-menu offset-y >
        <template v-slot:activator="{ on, attrs }">
          <v-btn :disabled="!ownerHasLoggedIn"
            outlined color="indigo"  x-small
            v-bind="attrs"
            v-on="on"
          >
              <font-awesome-icon icon="cog" />  
          </v-btn>
        </template>
        <v-list>
          <v-list-item @click="onClickEditEntryTitle()" :dense="true">
            <v-list-item-title class="text-left indigo--text"> <font-awesome-icon icon="pencil-alt" class="mr-1" />  Edit</v-list-item-title>
          </v-list-item>
          <v-list-item @click="onClickDeleteEntry()"  :dense="true" >
            <v-list-item-title class="text-left red--text" > <font-awesome-icon icon="trash-alt" class="mr-1" />  Delete</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
            </div>
            </div>
          </h3>
        </div>
      </div>
      <v-tabs v-model="tab" color="indigo">
        <v-tabs-slider></v-tabs-slider>
        <v-tab v-for="tabItem in tabItems" :key="tabItem">
          {{ tabItem }}
        </v-tab>
      </v-tabs>
      <v-tabs-items v-model="tab">
        <v-tab-item :key="0">
          <div>
            <v-container class="d-flex mb-n3 mt-n2" v-if="entryData">
             <div class="mr-auto"> 
                <div class="mr-2 indigo--text" v-show="entryData.items.length &&
                      getSumofItems(entryData.items, 'itemTotal')
                  ">
                <b>Total: ₱{{ getSumofItems(entryData.items, "itemTotal") }}</b>
                 
              </div>
             </div>
             <div>
               <v-btn class="pic-btn mr-9 mt-1" outlined color="indigo"  @click="setupPrintingArea('items')" x-small>
                      <font-awesome-icon icon="camera" />
                  </v-btn>
              
               <v-menu offset-y >
        <template v-slot:activator="{ on, attrs }">
          <v-btn :disabled="!ownerHasLoggedIn"
            outlined color="indigo" class="m-t-n-1" x-small
            v-bind="attrs"
            v-on="on"
          >
              <font-awesome-icon icon="cog" />  
          </v-btn>
        </template>
        <v-list>
          <v-list-item @click="onClickImportItems()" :dense="true">
            <v-list-item-title class="text-left indigo--text">  Import items</v-list-item-title>
          </v-list-item>
          <v-list-item @click="onClickListAddItemsToGroup()"  :dense="true" >
            <v-list-item-title class="text-left indigo--text" >  Save items to group</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
             </div>
            </v-container>
            <div class="mx-2 mb-2">
              <v-btn
                outlined
                block
                color="indigo"
                class="mt-2"
                v-show="!showEditInputItem"
                :disabled="!ownerHasLoggedIn"
                @click="onToggleEditInputItem(showEditInputItem)"
              >
                <font-awesome-icon icon="plus" size="sm" class="mr-1" /> ADD ITEM
              </v-btn>
            </div>
            <InputItemData
              @cancelAddItem="onToggleEditInputItem"
              @saveEditItem="saveItemValues" 
              :inputItemMode="showEditInputItem"
              :editItemData="editItemData"
              v-show="showEditInputItem"
            />
            <v-row class="mb-n4" >
          <v-col class="col-3 flex-column d-flex">
                  <small class="text-left ml-2">Name</small>
                </v-col>
          <v-col class="col-2 flex-column d-flex">
                  <small class="ml-n1 text-left">Amount</small>
                </v-col> 
          <v-col class="col-1 flex-column d-flex"> 
                </v-col>
          <v-col class="col-2 flex-column d-flex">
                  <small class="ml-n3 text-left">Qty</small>
                </v-col>
          <v-col class="col-3 flex-column d-flex">
                  <small class="mr-4 text-right">Total</small>
                </v-col>
        </v-row>
            <ItemList ref="itemlistref"
              v-if="entryData"
              :listdata="entryData.items"
              :drawerValue="personDrawer"
              :ownerHasLoggedIn="ownerHasLoggedIn"
              @activatePersonBtmDrawer="toggleItemBtmDrawer"
              @activateDeleteItem="activateDeleteItemDialog"
              @activateEditItem="activateEditItemDialog"
              @setActiveItem="setActiveItemDrawer"
            />
          </div>
        </v-tab-item>
        <v-tab-item :key="1">
          <div>
            <v-container class="d-flex mb-n3 mt-n2" v-if="entryData">
             <div class="mr-auto"> 
                <div class="mr-2 indigo--text" v-show="entryData.members.length &&
                      getSumofItems(entryData.members, 'totalPayable')
                  ">
                <b>Total: ₱{{ getSumofItems(entryData.members, "totalPayable") }}</b>
                 
              </div>
             </div>
             <div>
               <v-btn class="pic-btn mr-9 mt-1" outlined color="indigo"  @click="setupPrintingArea('members')" x-small>
                      <font-awesome-icon icon="camera" />
                  </v-btn>  
               <v-menu offset-y >
        <template v-slot:activator="{ on, attrs }">
          <v-btn :disabled="!ownerHasLoggedIn"
            outlined color="indigo" class="m-t-n-1" x-small
            v-bind="attrs"
            v-on="on"
          >
              <font-awesome-icon icon="cog" />  
          </v-btn>
        </template>
        <v-list>
          <v-list-item @click="onClickImportMember()" :dense="true">
            <v-list-item-title class="text-left indigo--text">  Import members</v-list-item-title>
          </v-list-item>
          <v-list-item @click="onClickListAddMembersToGroup()"  :dense="true" >
            <v-list-item-title class="text-left indigo--text" >  Save members to group</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu> 
             </div>
            </v-container>
            <div class="mx-2">
              <v-btn
                outlined
                block
                color="indigo"
                class="mt-2"
                v-if="!showEditInputPerson"
                :disabled="!ownerHasLoggedIn"
                @click="onToggleEditInputPerson(showEditInputPerson)"
              >
                <font-awesome-icon icon="plus" size="sm" class="mr-1" /> ADD MEMBER
              </v-btn>
            </div>
            <InputPersonData
              @cancelAddPerson="onToggleEditInputPerson"
              @saveEditPerson="saveUserValues"
              :editPersonData="editPersonData"
              :inputPersonMode="showEditInputPerson"
              v-show="showEditInputPerson"
            />
            <v-row class="mt-0" >
              <v-col class="col-8 flex-column d-flex">
                  <small class="text-left ml-2">Name</small>
              </v-col>
              <v-col class="col-3 flex-column d-flex">
                  <small class="mr-6 text-right">Total</small> 
              </v-col>
          </v-row> 
            <MemberList ref="memberlistref" :class="{ printingarea: isPrintMode }" 
              v-if="entryData"
              :listdata="entryData.members"
              :ownerHasLoggedIn="ownerHasLoggedIn"
              :drawerValue="itemDrawer"
              @activateItemBtmDrawer="togglePersonBtmDrawer"
              @activateDeletePerson="activateDeletePersonDialog"
              @activatePayment="activateBillPayment"
              @activateEditPerson="activateEditPersonDialog"
              @setActivePerson="setActivePersonDrawer"
            />
          </div>
        </v-tab-item>
      </v-tabs-items>
      <div>
        <v-navigation-drawer v-model="personDrawer" absolute bottom>
          <MemberListBottomDrawer
            v-if="itemParentMemberList"
            :listData="itemParentMemberList"
            :activeParentEntity="activeParentEntity"
            @onChangePersonDrawerCheckbox="addItemChildtoEntity"
          />
        </v-navigation-drawer>
        <v-navigation-drawer v-model="itemDrawer" absolute bottom>
          <ItemListBottomDrawer
            v-if="memberParentItemList"
            :listData="memberParentItemList"
            :activeParentEntity="activeParentEntity"
            @onChangeItemDrawerCheckbox="addItemChildtoEntity"
          />
        </v-navigation-drawer>
      </div>
      <ConfirmDeleteItem ref="confirm"></ConfirmDeleteItem>
      <ConfirmPayment ref="payment"></ConfirmPayment>
      <ItemsGroupDialog :importDialogData="itemDialogData" :showImportDialog="itemDialog"  @activateDialog="activateEntityDialog" @importItems="importItemsToList"></ItemsGroupDialog> 
      <MembersGroupDialog :importDialogData="memberDialogData" :showImportDialog="memberDialog" @activateDialog="activateEntityDialog" @importMembers="importMembersToList"></MembersGroupDialog> 
      <SaveMembersToGroup :membersList="entryData && entryData.members" :showSaveMembersModal="saveMembersModal" @showAddMembersToGroup="showAddMembersToGroupModal" @submitMembersToGroup="submitSelectedMembersToGroup"></SaveMembersToGroup>
      <SaveItemsToGroup :itemList="entryData && entryData.items" :showSaveItemsModal="saveItemsModal" @showAddItemsToGroup="showAddItemsToGroupModal" @submitItemsToGroup="submitSelectedItemsToGroup"></SaveItemsToGroup>
    </div>
</template>

<script>
  import {
    db
  } from "../main";
  import html2canvas from 'html2canvas';
  import mixin from "../plugins/mixin";
  import firebase from 'firebase/app';
  import InputItemData from "../components/InputItemData.vue";
  import ItemList from "../components/ItemList.vue";
  import InputPersonData from "../components/InputPersonData.vue";
  import MemberList from "../components/MemberList.vue";
  import MemberListBottomDrawer from "../components/MemberListBottomDrawer.vue";
  import ItemListBottomDrawer from "../components/ItemListBottomDrawer.vue";
  import ConfirmDeleteItem from "../components/ConfirmDeleteItem.vue";
  import ItemsGroupDialog from "../components/ItemsGroupDialog.vue";
  import MembersGroupDialog from "../components/MembersGroupDialog.vue";
  import SaveMembersToGroup from "../components/SaveMembersToGroup.vue";
  import SaveItemsToGroup from "../components/SaveItemsToGroup.vue";
  import ConfirmPayment from "../components/ConfirmPayment.vue";
  export default {
    name: "Splitter",
    data: () => ({
      entryData: null,
      loggedInUser: "",
      newEntryTitle: "",
      ownerHasLoggedIn: false,
      itemDialog: false,
      memberDialog: false,
      entryTitleEditMode: false, 
      isPrintMode: false,
      itemDialogData: [],
      memberDialogData: [],
      tab: null,
      members: [],
      tabItems: ["Items", "Members"],
      itemDrawer: false,
      personDrawer: false,
      showEditInputItem: false,
      showEditInputPerson: false,
      addItemsToMemberParent: false,
      saveMembersModal: false,
      saveItemsModal: false,
      group: null,
      activeParentEntity: {},
      openedPersonDrawer: {},
      openedItemDrawer: {},
      editItemData: {},
      editPersonData: {},
      itemParentMemberList: [],
      memberParentItemList: [],
    }),
    components: {
      InputItemData,
      InputPersonData,
      ItemList,
      MemberList,
      MemberListBottomDrawer,
      ItemListBottomDrawer,
      ConfirmDeleteItem,
      ItemsGroupDialog,
      MembersGroupDialog,
      SaveMembersToGroup,
      ConfirmPayment,
      SaveItemsToGroup
    },
    mixins: [mixin],
    mounted: function() {
      this.entryID = this.$route.params.id;
      this.fetchEntryData();
    },
    watch: {
      entryData(newData) {
        this.loggedInUser = firebase.auth().currentUser;
        this.ownerHasLoggedIn = this.loggedInUser && newData.ownerId === this.loggedInUser.uid;
      }
    },
    methods: {
      async fetchEntryData() {
        await db
          .collection("entries")
          .doc(this.entryID)
          .get()
          .then((snapshot) => {
            this.entryData = snapshot.data();
          })
          .catch((error) => {
            console.error(error);
          });
      },
      saveItemValues(value) {
        let updateData = null;
        if (value.id) {
          let findEditItem = this.entryData.items.find((x) => x.id === value.id);
          findEditItem.name = value.newItemName;
          findEditItem.itemAmount = parseFloat(value.newItemAmount).toFixed(2);
          findEditItem.itemTotal = parseFloat(parseFloat(findEditItem.itemAmount) * findEditItem.itemTotalQty).toFixed(2);
          findEditItem.memberList.forEach(element => {
            element.itemAmount = findEditItem.itemAmount;
            element.itemTotal = parseFloat(parseFloat(element.itemAmount) * element.itemQty).toFixed(2);
          });
          this.entryData.members.forEach(element => {
            let findEditItem = element.itemList.find((x) => x.id === value.id);
            if (findEditItem) {
              findEditItem.name = value.newItemName;
              findEditItem.itemAmount = parseFloat(value.newItemAmount).toFixed(2);
              findEditItem.itemTotal = parseFloat(parseFloat(value.newItemAmount) * findEditItem.itemQty).toFixed(2);
            }
            element.totalPayable = this.getSumofItems(element.itemList, "itemTotal");
          });
          updateData = {
            items: this.entryData.items,
            members: this.entryData.members
          };
        } else {
          updateData = {
            items: [{
                id: this.getUniqueID(),
                name: value.newItemName,
                itemAmount: parseFloat(value.newItemAmount).toFixed(2),
                itemTotal: "",
                itemTotalQty: 0,
                isItemShared: false,
                memberList: [],
              },
              ...this.entryData.items,
            ],
          };
        }
        var entryRef = db.collection("entries").doc(this.entryID);
        return entryRef
          .update(updateData)
          .then(() => {
            this.fetchEntryData(); 
            this.activateEditItemDialog({showInput: true});  
          })
          .catch((error) => {
            console.error("Error updating document: ", error);
          });
      },
      saveUserValues(value) {
        let updateData = null;
        if (value.id) {
          let findEditPerson = this.entryData.members.find((x) => x.id === value.id);
        findEditPerson.name = value.newPersonName;

         this.entryData.items.forEach(element => {
            let findEditPerson = element.memberList.find((x) => x.id === value.id);
            if (findEditPerson) {
              findEditPerson.name = value.newPersonName; 
            } 
          });
          updateData = {
            items: this.entryData.items,
            members: this.entryData.members
          };
        } else {
          updateData = {
            members: [{
                id: this.getUniqueID(),
                name: value.newPersonName,
                totalPayable: "",
                itemList: [],
              },
              ...this.entryData.members,
            ],
          };
        }
        var entryRef = db.collection("entries").doc(this.entryID);
        return entryRef
          .update(updateData)
          .then(() => {
            this.fetchEntryData(); 
            this.activateEditPersonDialog({showInput: true});   
          })
          .catch((error) => {
            console.error("Error updating document: ", error);
          });
      },
      addItemChildtoEntity(drawerItemsList) {
        let {
          drawerList,
          activeParentEntity,
          isItemShared
        } = drawerItemsList;
        this[this.addItemsToMemberParent ? "itemDrawer" : "personDrawer"] = false;
        let childField = this.addItemsToMemberParent ? "itemList" : "memberList",
          otherEntityChanges = [];
        drawerList.map((bottomDrawerEntity) => {
          let newChildEntity = null;
          if (bottomDrawerEntity.isChecked) {
            let itemAmount = this.addItemsToMemberParent ?
              bottomDrawerEntity.itemAmount :
              activeParentEntity.itemAmount;
            newChildEntity = {
              id: bottomDrawerEntity.id,
              name: bottomDrawerEntity.name,
              itemAmount: itemAmount,
              itemQty: isItemShared ? 1 : bottomDrawerEntity.currentQty,
              itemTotal: parseFloat(
                parseFloat(itemAmount) * bottomDrawerEntity.currentQty
              ).toFixed(2),
            };
            otherEntityChanges.push(newChildEntity);
          } else {
            // bottomDrawerEntity.itemTotal = "0";
            // bottomDrawerEntity.itemTotalQty = 0;
          }
          activeParentEntity[childField] = otherEntityChanges;
          if (newChildEntity) return newChildEntity;
        });
        if (isItemShared && drawerItemsList.sharedQty) {
          activeParentEntity.isItemShared = isItemShared;
          let totalAmountToPay = parseFloat(parseFloat(activeParentEntity.itemAmount) * +drawerItemsList.sharedQty).toFixed(2), 
          divideEqually = parseFloat(
            totalAmountToPay / otherEntityChanges.length
          ).toFixed(2);
          activeParentEntity.itemTotalQty = +drawerItemsList.sharedQty; 
          activeParentEntity.itemTotal = totalAmountToPay;
          activeParentEntity[childField].forEach(function(item) {
            item.itemAmount = totalAmountToPay;
            item.itemTotal = divideEqually;
            item.itemQty = otherEntityChanges.length;
          });
        } else {
          if (this.addItemsToMemberParent) {
            activeParentEntity.totalPayable = this.getSumofItems(
              activeParentEntity[childField],
              "itemTotal"
            );
          } else {
            activeParentEntity.itemTotalQty = parseInt(
              this.getSumofItems(activeParentEntity[childField], "itemQty")
            );
            activeParentEntity.itemTotal = this.getSumofItems(
              activeParentEntity[childField],
              "itemTotal"
            );
          }
        }
        let otherMainEntity = this.addItemsToMemberParent ?
          this.entryData.items :
          this.entryData.members,
          otherChildField = this.addItemsToMemberParent ?
          "memberList" :
          "itemList";
        otherMainEntity.forEach((otherParentEntity, i) => {
          let isParentEntityModified = otherEntityChanges.find(x => x.id === otherParentEntity.id);
          if (isParentEntityModified) { //change value or add a child to the Items/Members list
            let isChildAlreadyInList = otherMainEntity[i][otherChildField].find(
              (x) => x.id === activeParentEntity.id
            );
            if (isChildAlreadyInList) {
              isChildAlreadyInList.itemAmount = isParentEntityModified.itemAmount;
              isChildAlreadyInList.itemQty = isItemShared ? 1 : isParentEntityModified.itemQty;
              isChildAlreadyInList.itemTotal = isParentEntityModified.itemTotal;
              isChildAlreadyInList.isItemShared =  (typeof isItemShared !== "undefined" ? isItemShared : false);
            } else {
              otherMainEntity[i][otherChildField].push({
                id: activeParentEntity.id,
                name: activeParentEntity.name,
                itemAmount: isParentEntityModified.itemAmount,
                itemQty: isItemShared ? otherEntityChanges.length : isParentEntityModified.itemQty,
                itemTotal: isParentEntityModified.itemTotal,
                isItemShared: (typeof isItemShared !== "undefined" ? isItemShared : false)
              });
            }
            if (!this.addItemsToMemberParent) {
              otherParentEntity.totalPayable = this.getSumofItems(
                otherMainEntity[i][otherChildField],
                "itemTotal"
              );
            } else {
              otherParentEntity.itemTotalQty = parseInt(
                this.getSumofItems(otherMainEntity[i][otherChildField], "itemQty")
              );
              otherParentEntity.itemTotal = this.getSumofItems(
                otherMainEntity[i][otherChildField],
                "itemTotal"
              );
            }
          } 
          //removing item from the other main list
          let otherParentEntityHasActiveDrawerItemIndex = otherParentEntity[otherChildField].findIndex(x => x.id === activeParentEntity.id);
          if (otherParentEntityHasActiveDrawerItemIndex >= 0){
            let doesActiveParentEntityHaveCurrentOtherParentEntity = activeParentEntity[childField].find(x=> x.id === otherParentEntity.id);
            if (!doesActiveParentEntityHaveCurrentOtherParentEntity){
              otherParentEntity[otherChildField].splice(otherParentEntityHasActiveDrawerItemIndex, 1);
              otherParentEntity.totalPayable = this.getSumofItems(otherParentEntity[otherChildField], "itemTotal");
              otherParentEntity.itemTotalQty = +this.getSumofItems(otherParentEntity[otherChildField], "itemQty");
              otherParentEntity.itemTotal = this.getSumofItems(otherParentEntity[otherChildField], "itemTotal");
            }
          }
        });
        this.updateEntry({
          members: this.entryData.members,
          items: this.entryData.items,
        }); 
      },
      getSumofItems(listData, fieldName) {
        let aggregatedVal = 0;
        for (let listItem of listData) {
          if (listItem[fieldName])
            aggregatedVal = (
              parseFloat(aggregatedVal) + parseFloat(listItem[fieldName])
            ).toFixed(2);
        }
        return aggregatedVal;
      },
      sortByKey(array, key) {
        return array.sort(function(a, b) {
          var x = a[key];
          var y = b[key];
          return ((x < y) ? -1 : ((x > y) ? 1 : 0));
        });
      },
      getUniqueID() {
        return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(
          c
        ) {
          var r = (Math.random() * 16) | 0,
            v = c == "x" ? r : (r & 0x3) | 0x8;
          return v.toString(16);
        });
      },
      toggleItemBtmDrawer(drawerValue) {
        this.addItemsToMemberParent = false;
        this.personDrawer = drawerValue;
      },
      togglePersonBtmDrawer(drawerValue) {
        this.addItemsToMemberParent = true;
        this.itemDrawer = drawerValue;
      },
      updateEntry(updateData, callback) {
        let entryRef = db.collection("entries").doc(this.entryID);
        return entryRef
          .update(updateData)
          .then(() => {
            this.fetchEntryData();
            if (callback) callback();
          })
          .catch((error) => {
            console.error("Error updating document: ", error);
          });
      }, 
      setActivePersonDrawer(person) {
        let drawerList = this.entryData.items.map(function(item) {
          let isChildInList = person.itemList.find((x) => x.id === item.id);
          item.currentQty = isChildInList ? isChildInList.itemQty : 0;
          item.isChecked = Boolean(isChildInList);
          return item;
        });
        this.activeParentEntity = person;
        this.memberParentItemList = drawerList;
      },
      setActiveItemDrawer(item) {
        let drawerList = this.entryData.members.map(function(member) {
          let isChildInList = item.memberList.find((x) => x.id === member.id);
          member.currentQty = isChildInList ? isChildInList.itemQty : 0;
          member.isChecked = Boolean(isChildInList);
          return member;
        });
        this.activeParentEntity = item;
        this.itemParentMemberList = drawerList;
      },
      onToggleEditInputItem(showEditItem) {
        this.showEditInputItem = !showEditItem;
        if (!this.showEditInputItem) this.inputItemMode = false; 
      },
      onToggleEditInputPerson(showEditPerson) {
        this.showEditInputPerson = !showEditPerson;
        if (!this.showEditInputPerson) this.inputPersonMode = false;
      },
      activateEditItemDialog(item) { 
        let editMode = Boolean(item && item.id); 
        this.editItemData = item;
        this.inputItemMode = editMode;
        this.showEditInputItem = editMode || item.showInput;
      },
      activateEditPersonDialog(person) {
        let editMode = Boolean(person && person.id); 
        this.editPersonData = person;
        this.inputPersonMode = editMode;
        this.showEditInputPerson = editMode || person.showInput;
      },
      onClickImportItems() {  
        let self = this;
        this.getSubCollectionData({
          collection: "userData", docId: this.loggedInUser.uid, 
          subcollection: "itemGroups", 
          callback: function (response){
            if (response && response.data){ 
              self.activateEntityDialog({dataName: "itemDialogData", name: "itemDialog", data: response.data, value: true })
            }
        }})
      }, 
      onClickListAddItemsToGroup() { 
        this.saveItemsModal = true;
       }, 
      showAddItemsToGroupModal(dataoptions) {   
        this.saveItemsModal = !(dataoptions && dataoptions.hideModal);
      },  
      submitSelectedItemsToGroup(dataoptions) {    
        let self = this, createdOnDate = firebase.firestore.FieldValue.serverTimestamp();
        db.collection('userData').doc(this.loggedInUser.uid).collection('itemGroups').add({
            name: dataoptions.groupName,
            createdOn: createdOnDate,
            children: dataoptions.children
        }).then(() => {  
           self.showAddItemsToGroupModal({hideModal: true});  
        }).catch((error) => {
            console.error("Error adding document: ", error);
        }); 
      }, 
      onClickSaveItemsToList() { 
        this.activateEntityDialog(
          {dataName: "childrenList", 
          data: this.entryData.items,
          name: "showSaveItemsToList",
          value: true }
        );
       }, 
      confirmSaveItemsToList() {  
        let self = this;
        let childrenData = this.entryData.items.map(function (d){
          return { id: self.getUniqueID(), itemAmount: d.itemAmount, name: d.name};
        });  

       let createdOnDate = firebase.firestore.FieldValue.serverTimestamp();
        db.collection('userData').doc(this.loggedInUser.uid).collection('itemGroups').add({
            name: "Sample",
            createdOn: createdOnDate,
            children: childrenData
        }).then(() => {
            self.newItemName = "";
            self.showEditInputItem = false;
            self.fetchItems(); 
        }).catch((error) => {
            console.error("Error adding document: ", error);
        });
      },
      onClickImportMember() {  
        let self = this;
        this.getSubCollectionData({
          collection: "userData", docId: this.loggedInUser.uid, 
          subcollection: "memberGroups", 
          callback: function (response){
            if (response && response.data){ 
              self.activateEntityDialog({dataName: "memberDialogData", name: "memberDialog", data: response.data, value: true })
            }
        }})
      }, 
      activateEntityDialog(dataOptions){ 
        if (dataOptions && dataOptions.dataName && dataOptions.data) this[dataOptions.dataName] = dataOptions.data; 
        this[dataOptions.name] = dataOptions.value; 
      },
      importMembersToList(dataOptions){ 
        let foundMembers = [];
        for (let i = 0; i < dataOptions.selected.length; i++){
          let selectedId = dataOptions.selected[i], importItemList = dataOptions.list; 
          for (let j = 0; j < importItemList.length; j++){
             let findItem = importItemList[j].children.find(x => x.id === selectedId), 
              isUserAdded = findItem && foundMembers.length && foundMembers.find(x=> x.name === findItem.name);
             if (findItem && !isUserAdded){
               let cloneItemObj = JSON.parse(JSON.stringify(findItem));
               cloneItemObj.id = this.getUniqueID();
               cloneItemObj.totalPayable = "";  
               cloneItemObj.itemList = []; 
               this.entryData.members.push(cloneItemObj);   
               foundMembers.push(cloneItemObj);
             }
             if (dataOptions.selected.length === foundMembers.length) break;
          }  
        } 
        let self = this;
        this.updateData({collection: "entries", docId: this.entryID, requestData: {members: this.entryData.members}, 
        callback: function(){
          self.fetchEntryData();
        }})
       },
      importItemsToList(dataOptions){  
        let foundItems = [];
        for (let i = 0; i < dataOptions.selected.length; i++){
          let selectedId = dataOptions.selected[i], importItemList = dataOptions.list; 
          for (let j = 0; j < importItemList.length; j++){
             let findItem = importItemList[j].children.find(x => x.id === selectedId);
             if (findItem){
               let cloneItemObj = JSON.parse(JSON.stringify(findItem));
               cloneItemObj.id = this.getUniqueID();
               cloneItemObj.itemTotal = ""; 
               cloneItemObj.itemTotalQty = 0; 
               cloneItemObj.isItemShared = false; 
               cloneItemObj.memberList = []; 
               this.entryData.items.push(cloneItemObj);   
               foundItems.push(cloneItemObj);
             }
             if (dataOptions.selected.length === foundItems.length) break;
          }  
        } 
        let self = this;
        this.updateData({collection: "entries", docId: this.entryID, requestData: {items: this.entryData.items}, 
        callback: function(){
          self.fetchEntryData();
        }})
      },
      onClickListAddMembersToGroup() {   
        this.saveMembersModal = true;
      },  
      showAddMembersToGroupModal(dataoptions) {   
        this.saveMembersModal = !(dataoptions && dataoptions.hideModal);
      }, 
      submitSelectedMembersToGroup(dataoptions) {    
        let self = this, createdOnDate = firebase.firestore.FieldValue.serverTimestamp();
        db.collection('userData').doc(this.loggedInUser.uid).collection('memberGroups').add({
            name: dataoptions.groupName,
            createdOn: createdOnDate,
            children: dataoptions.children
        }).then(() => {  
           self.showAddMembersToGroupModal({hideModal: true});  
        }).catch((error) => {
            console.error("Error adding document: ", error);
        }); 
      }, 
      async activateDeleteItemDialog(item) {
        if (
          await this.$refs.confirm.open({
            title: "Delete",
            message:  `Are you sure you want to remove ${item.name}?`,
            options: {
              color: "red",
            }
          }  
          )
        ) {
          let updatedMembers = this.entryData.members.map((member) => {
            member.itemList = member.itemList.filter((x) => x.id !== item.id);
            member.totalPayable = this.getSumofItems(member.itemList, "itemTotal");
            return member;
          });
          let updatedItems = this.entryData.items.filter(
            (x) => x.id !== item.id
          ); 
          this.updateEntry({
            items: updatedItems,
            members: updatedMembers,
          });
        } else {
          console.log("dialog unavailable");
        }
      },
      async activateDeletePersonDialog(person) {
        if (
          await this.$refs.confirm.open({
            title: "Delete",
            message:  `Are you sure you want to remove ${person.name}?`,
            options: {
              color: "red",
            }
          } 
          )
        ) {
          let updatedMembers = this.entryData.members.filter(
            (x) => x.id !== person.id
          );
          let updatedItems = this.entryData.items.map((item) => {
            item.memberList = item.memberList.filter((x) => x.id !== person.id);
            item.itemTotalQty = parseInt(
              this.getSumofItems(item.memberList, "itemQty")
            );
            item.itemTotal = this.getSumofItems(item.memberList,
              "itemTotal"
            );
            return item;
          }); 
          this.updateEntry({
            items: updatedItems,
            members: updatedMembers,
          });
        } else {
          console.log("dialog unavailable");
        }
      },
      async activateBillPayment(person) {
        if (
          await this.$refs.payment.open({
            title: "Pay",
            message:  `Paid by: ${person.name}?`,
            options: {
              color: "green",
            }
          } 
          )
        ) {
          let updatedMembers = this.entryData.members.filter(
            (x) => x.id !== person.id
          );
          let updatedItems = this.entryData.items.map((item) => {
            item.memberList = item.memberList.filter((x) => x.id !== person.id);
            item.itemTotalQty = parseInt(
              this.getSumofItems(item.memberList, "itemQty")
            );
            item.itemTotal = this.getSumofItems(item.memberList,
              "itemTotal"
            );
            return item;
          }); 
          this.updateEntry({
            items: updatedItems,
            members: updatedMembers,
          });
        } else {
          console.log("dialog unavailable");
        }
      },
      onClickEditEntryTitle: function(){ 
        this.entryTitleEditMode = !this.entryTitleEditMode;
        if (this.entryTitleEditMode){
          this.$nextTick(() => {
              this.newEntryTitle = this.entryData.title;
              this.$refs.entryTitle.focus();
            }) 
        }
      },
      saveEntryTitle: function(){  
        let self = this;
        this.updateEntry({
            title: this.newEntryTitle
          }, function (){
            self.entryTitleEditMode = !self.entryTitleEditMode;
            self.updateSubCollectionData({
            collection: "userData", 
            docId: self.loggedInUser.uid,
            subcollection: "entries",
            subcollectionDocId: self.entryID, 
            requestData: {  
              title: self.newEntryTitle
            }});
          });
      },
      onClickDeleteEntry: function(){
        this.$refs.confirm.open(
          {title: 'Delete', message: `Delete ${this.entryData.title} and its contents?`, 
          options: {color: 'red'} }).then((confirm) => {
            if (confirm){
                let self = this;
                 this.deleteCollectionData({
                      collection: "entries",
                      docId: self.entryID, 
                      callback: function() {
                        self.deleteSubCollectionData({
                              collection: "userData",
                              docId: self.loggedInUser.uid,
                              subcollection: "entries",
                              subcollectionDocId: self.entryID, 
                              callback: function() {
                                  let self2 = self;
                                  self2.deleteCollectionData({
                                      collection: "userData",
                                      docId: self2.entryID, 
                                      callback: function() {
                                          self2.$router.push({ path: '/' });
                                      }
                                  });  
                              }
                          });  
                      }
                  });  
            }
        });  
      },
      setupPrintingArea(type) { 
          this.isPrintMode = !this.isPrintMode;
          this.$nextTick(() => {  
              this.printThis(this, type);
        })  
      },
      async printThis(printview, type) { 
          const el = type === 'items' ?printview.$refs.itemlistref.$refs.printcontent : printview.$refs.memberlistref.$refs.printcontent;
          const options = {
              type: "dataURL",
          };
          const printCanvas = await html2canvas(el, options);
          const link = document.createElement("a");
          link.setAttribute("download", "download.png");
          link.setAttribute(
              "href",
              printCanvas
              .toDataURL("image/png")
              .replace("image/png", "image/octet-stream")
          );
          link.click();
           this.isPrintMode = !this.isPrintMode;
      },
    },
  };
</script>

