import Vue from "vue";
import App from "./App.vue";
import vuetify from "./plugins/vuetify";
import router from "./router";
import firebase from "firebase/app";
import "firebase/firestore";
import VueHtml2Canvas from "vue-html2canvas";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faTimes,
  faPlus,
  faCheck,
  faUserFriends,
  faUtensils,
  faPencilAlt,
  faTrashAlt,
  faCog,
  faArrowLeft,
  faCamera,
  faMinus,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

Vue.component("font-awesome-icon", FontAwesomeIcon);
Vue.use(VueHtml2Canvas);
library.add(
  faTimes,
  faPlus,
  faCheck,
  faUserFriends,
  faUtensils,
  faPencilAlt,
  faCog,
  faArrowLeft,
  faMinus,
  faCamera,
  faTrashAlt
);
let app = "";
const firebaseConfig = {
  apiKey: "AIzaSyDUE7TUA3GuyJiwkJl1wr5vSSGJlhvG2qM",
  authDomain: "splitter-33929.firebaseapp.com",
  projectId: "splitter-33929",
  storageBucket: "splitter-33929.appspot.com",
  messagingSenderId: "48364131169",
  appId: "1:48364131169:web:88a33451a6a59a2643ab20",
  measurementId: "G-T74C4YWWD6",
};
// Initialize Firebase
export default firebase.initializeApp(firebaseConfig);
Vue.config.productionTip = false;
firebase.auth().onAuthStateChanged(() => {
  if (!app) {
    /* eslint-disable no-new */
    app = new Vue({
      vuetify,
      router,
      render: (h) => h(App),
    }).$mount("#app");
  }
});
export const db = firebase.firestore();
