import { db } from "../main";
export default {
  data() {},
  methods: {
    async getData(dataOptions) {
      await db
        .collection(dataOptions.collection)
        .doc(dataOptions.docId)
        .get()
        .then((snapshot) => {
          dataOptions.callback &&
            dataOptions.callback({ data: snapshot.data() });
        })
        .catch((error) => {
          console.error(error);
          dataOptions.callback && dataOptions.callback({ error: error });
        });
    },
    async getSubCollectionData(dataOptions) {
      await db
        .collection(dataOptions.collection)
        .doc(dataOptions.docId)
        .collection(dataOptions.subcollection)
        .get()
        .then((snapshot) => {
          let dataList = [];
          snapshot.forEach((doc) => {
            const entry = doc.data();
            entry.id = doc.id;
            dataList.push(entry);
          });
          dataOptions.callback && dataOptions.callback({ data: dataList });
        })
        .catch((error) => {
          console.error(error);
          dataOptions.callback && dataOptions.callback({ error: error });
        });
    },
    updateData(dataOptions) {
      let entryRef = db
        .collection(dataOptions.collection)
        .doc(dataOptions.docId);
      return entryRef
        .update(dataOptions.requestData)
        .then(() => {
          dataOptions.callback && dataOptions.callback();
        })
        .catch((error) => {
          console.error("Error updating document: ", error);
        });
    },
    updateSubCollectionData(dataOptions) {
      db.collection(dataOptions.collection)
        .doc(dataOptions.docId)
        .collection(dataOptions.subcollection)
        .doc(dataOptions.subcollectionDocId)
        .update(dataOptions.requestData)
        .then(() => {
          dataOptions.callback && dataOptions.callback();
        })
        .catch((error) => {
          console.error("Error updating document: ", error);
        });
    },
    deleteCollectionData(dataOptions) {
      db.collection(dataOptions.collection)
        .doc(dataOptions.docId)
        .delete()
        .then(() => {
          dataOptions.callback && dataOptions.callback();
        })
        .catch((error) => {
          console.error("Error updating document: ", error);
        });
    },
    deleteSubCollectionData(dataOptions) {
      db.collection(dataOptions.collection)
        .doc(dataOptions.docId)
        .collection(dataOptions.subcollection)
        .doc(dataOptions.subcollectionDocId)
        .delete()
        .then(() => {
          dataOptions.callback && dataOptions.callback();
        })
        .catch((error) => {
          console.error("Error updating document: ", error);
        });
    },
    getUniqueID() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(
        c
      ) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
    },
    formatDate(date) {
      if (!date) return "";
      date = new Date(date.toDate());
      var strArray = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ];
      var d = date.getDate();
      var m = strArray[date.getMonth()];
      var y = date.getFullYear();
      return "" + (d <= 9 ? "0" + d : d) + " " + m + " " + y;
    },
  },
};
