<template>
    <div >
        <div class="d-flex mt-2">  
            <div class=" mr-auto pl-4"> {{this.activeParentEntity &&this.activeParentEntity.name }} </div> 
            <div class=" "> <v-btn class="ml-2 mr-2" small outlined x color="indigo" @click.stop="onSaveList()"  > 
            Save
            </v-btn></div> 
        </div> 
        <v-list nav dense class="bottom-drawer custom-scroll" :key="componentKey">
            <v-list-item-group active-class="deep-purple--text text--accent-4" v-if="listData.length">
                <v-list-item v-for="item in listData" :key="item.id" >
                    <v-list-item-subtitle >
                        <v-checkbox v-model="item.isChecked" dense @change="onClickListItem(item)"
                        :label="`${item.name} (₱${item.itemAmount})`" 
                        class="" > </v-checkbox> 
                  <div class="drawer-actions-container">
                      <div class="d-flex drawer-actions">
                          <v-btn @click.stop="onClickDecreaseQty(item)" :disabled="item.currentQty === 0"  fab color="indigo" depressed  class="xxs-btn">
                            <font-awesome-icon icon="minus" class="xxs-btnicon" color="white"/>
                        </v-btn>
                      </div>
                      <div class="d-flex drawer-actions drawer-qty">
                          <v-text-field v-model="item.currentQty" dense outlined class="mt-n2"></v-text-field>
                           </div>
                      <div class="d-flex drawer-actions">
                          <v-btn @click.stop="onClickIncreaseQty(item)" fab color="indigo" depressed class="xxs-btn">
                            <font-awesome-icon icon="plus" class="xxs-btnicon" color="white"/>
                        </v-btn>
                        </div>
                  </div>
                    </v-list-item-subtitle>
                </v-list-item>
            </v-list-item-group>
        </v-list>
    </div>
</template>

<script>
     export default {
         data: () => ({  
             bottomDrawerList: [], 
            componentKey: 0,
         }),
         props: {
             listData: {
                 type: Array,
             }, 
             activeParentEntity: {
                 type: Object,
             } 
         },
         watch: {
             listData(newValue) { 
                this.listData = newValue;
                  this.bottomDrawerList = newValue;
             } 
         }, 
         methods: {
            forceRerender() {
                this.componentKey += 1;
            }, 
             onSaveList: function(){  
                this.$emit('onChangeItemDrawerCheckbox',{
                    drawerList: this.listData,
                    activeParentEntity: this.activeParentEntity 
                });  
             },
             onClickListItem: function(item){  
                item.currentQty = !item.isChecked && item.currentQty !== 0 ? 0 : 1;
                this.forceRerender();
             },
             onClickDecreaseQty: function(item){  
                item.currentQty--; 
                if (!item.currentQty) item.isChecked = false; 
                this.forceRerender();
             },
             onClickIncreaseQty: function(item){ 
                item.currentQty++; 
                if (item.currentQty && !item.isChecked) item.isChecked = true;
                this.forceRerender();
             }
         }
     }
</script>
